import { Component, OnInit, ElementRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MatSnackBar } from '@angular/material';

import { NotificationService } from '../../_shared/services/notification.service';
import { ApiService } from '../../_shared/services/api.service';

@Component({
  selector: 'app-form',
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.scss']
})
export class FormComponent implements OnInit {
  busy: boolean = false;
  name: string = "Patient Name";
  apointments: Array<any>;

  patient: any;
  

  constructor(
    public snackBar: MatSnackBar,
    private api: ApiService,
    private notification: NotificationService,
    private route: ActivatedRoute
  ) {
    this.apointments = new Array<any>();

    this.notification.connection.on("AppointmentCreated", (data) => {
      if (data.patientId !== this.patient.pkPersonId) {
        return;
      }

      this.api.get(`Apointments/${data.appointmentId}`).subscribe(data => {
        let appointment = data.body;

        this.apointments.unshift(appointment);

        this.snackBar.open(`New Appointment From ${appointment.fkPatient.fullName}`, null, {
          duration: 2000
        });
      });
    });
  }

  ngOnInit() {
    this.busy = true;
    this.api.get(`People/${this.route.snapshot.params.id}`).subscribe(result => {
      this.busy = false;
      if (result.status === "ERROR") {
        this.snackBar.open(result.message, null, {
          duration: 2000
        });
        return;
      }

      this.name = result.body.fullName;
      this.patient = result.body;
      
    });

    this.api.get(`Apointments/Patient/${this.route.snapshot.params.id}`).subscribe(result => {
      this.busy = false;
      this.apointments = result.body;
    });
  }

  ngOnDestroy() {
    this.notification.connection.off("AppointmentCreated")
  }

  calculateAge(birthday) { 
    var bday = new Date(birthday);
    var ageDifMs = Date.now() - bday.getTime();
    var ageDate = new Date(ageDifMs); // miliseconds from epoch
    return Math.abs(ageDate.getUTCFullYear() - 1970);
  }

}
