import { Component, OnInit, ViewChild } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';

import { MatSnackBar } from '@angular/material';

import { tap, catchError } from 'rxjs/operators';

import { ApiService } from '../../_shared/services/api.service';
import { AccountService } from '../../_shared/services/account.service';
import { TagInputComponent } from '../../_shared/components/tag-input/tag-input.component';

@Component({
  selector: 'app-form',
  templateUrl: './form.component.html',
  styleUrls: ['./form.component.scss']
})
export class FormComponent implements OnInit {
  @ViewChild("diagnosisTags") tagInput: TagInputComponent;

  constructor(
    public snackBar: MatSnackBar,
    private account: AccountService,
    private api: ApiService,
    private route: ActivatedRoute,
    private location: Location
  ) {
    this.busy = false;
    this.name = "Patient Name";
  }

  ngOnInit() {
    this.busy = true;
    this.api.get(`Apointments/${this.route.snapshot.params.id}`).subscribe(result => {
      this.busy = false;

      if (result.status === "ERROR") {
        this.snackBar.open(result.message, null, {
          duration: 2000
        });
        return;
      }

      this.name = result.body.fkPatient.fullName;
      this.apointment = result.body;

      this.isClosed = this.apointment.isClosed;
      this.isApproved = this.apointment.isApproved;
      this.doctorsNotes = this.apointment.doctorsNotes;
      this.diagnosy = this.apointment.diagnosy;

      if (this.apointment.diagnosis.length > 0) {
        this.tagInput.tags = this.apointment.diagnosis.map(diagnosis => {
          return diagnosis.name;
        })
      }
    });
  }

  busy: boolean;
  name: string;
  apointment: any;

  isClosed: boolean = false;
  isApproved: boolean = false;
  doctorsNotes: string;
  diagnosy: string;

  close() {
    this.apointment.fkCloserId = this.account.detail.fkPersonId;
    this.apointment.isClosed = true;
    this.save();
  }

  approve() {
    this.apointment.isApproved = true;
    this.save();
  }

  reject() {
    this.apointment.isRejected = true;
    this.save();
  }

  save() {
    this.busy = true;

    this.apointment.doctorsNotes =  this.doctorsNotes;
    /*this.apointment.diagnosis = this.tagInput.tags.map(tag => {
      return {
        name: tag
      };
    });*/
    this.apointment.diagnosy = this.diagnosy;

    this.api.put("Apointments", this.apointment)
    .pipe(
      tap((result) => {
        this.busy = false;
        this.snackBar.open("Appointment successfully updated", null, {
          duration: 3000
        });
      }),
      catchError((err, cought) => {
        this.busy = false;
        this.snackBar.open("An error accoured while saving appointment", null, 
        {
          duration: 3000
        });

        throw err;
      })
    )
    .subscribe(result => {
      this.location.back();
    });
  }

  back() {
    this.location.back();
  }

  calculateAge(birthday) { 
    var bday = new Date(birthday);
    var ageDifMs = Date.now() - bday.getTime();
    var ageDate = new Date(ageDifMs); // miliseconds from epoch
    return Math.abs(ageDate.getUTCFullYear() - 1970);
  }
}
