import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { Observable } from 'rxjs';

import { environment as Env } from '../../../environments/environment';

@Injectable({
    providedIn: 'root'
})
export class ApiService {
    private headers: HttpHeaders;

    constructor(private http: HttpClient) {
        this.headers = new HttpHeaders();
        this.addHeader('Content-Type', 'application/json; charset=utf-8');
    }

    get Url() { return Env.api.url };

    get Version() { return Env.api.version }

    public addHeader(key: string, value: string) {
        if (this.headers.has(key)) {
            this.headers = this.headers.set(key, value);
        } else {
            this.headers = this.headers.append(key, value);
        }
    }

    public get(path: string, options?: any): Observable<any> {
        options = Object.assign({}, options, {
            headers: this.headers
        });

        return this.http.get(`${Env.api.url}/${path}`, options);
    }

    public post(path: string, payload: any): Observable<any> {
        return this.http.post(`${Env.api.url}/${path}`, JSON.stringify(payload), { headers: this.headers });
    }

    public put(path: string, payload: any): Observable<any> {
        return this.http.put(`${Env.api.url}/${path}`, JSON.stringify(payload), { headers: this.headers });
    }
}