import { Component, OnInit } from '@angular/core';

import { MatSnackBar } from '@angular/material';

import { AccountService } from '../../_shared/services/account.service';
import { NotificationService } from '../../_shared/services/notification.service';
import { ApiService } from '../../_shared/services/api.service';

@Component({
  selector: 'appointment-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {
  constructor(
    public snackBar: MatSnackBar,
    private account: AccountService,
    private notification: NotificationService,
    private api: ApiService
  ) {
    this.busy = false;
    this.apointments = new Array<any>();

    this.notification.connection.on("AppointmentCreated", (data) => {
      this.api.get(`Apointments/${data.appointmentId}`).subscribe(data => {
        let appointment = data.body;

        //this.apointments.unshift(appointment);
        this.apointments.push(appointment);

        this.snackBar.open(`New Appointment From ${appointment.fkPatient.fullName}`, null, {
          duration: 2000
        });
      });
    });
  }

  ngOnInit() {
    this.busy = true;
    this.api.get("Apointments/open").subscribe(result => {
      this.busy = false;
      this.apointments = result.body;
    });
  }

  ngOnDestroy() {
    this.notification.connection.off("AppointmentCreated")
  }

  busy: boolean;
  apointments: Array<any>;

  get user() {
    return this.account.detail;
  }  

  get approved() {
    return this.apointments.filter(e => e.isApproved);
  }

  get pending() {
    return this.apointments.filter(e => !e.isApproved && !e.isRejected);
  }

  create() { }

  dateChanged() {
    console.log('date changed');
  }

  approve() {
  }

  reject() {
  }
}
