import { Component, OnInit, ViewChild } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';

import { MatSnackBar } from '@angular/material';

import { tap, catchError } from 'rxjs/operators';

import { Guid } from '../_shared/utils/guid';
import { ApiService } from '../_shared/services/api.service';
import { AccountService } from '../_shared/services/account.service';
import { TagInputComponent } from '../_shared/components/tag-input/tag-input.component';

import { FormGroup, FormControl, Validators } from '@angular/forms';
import {MatAutocompleteSelectedEvent, MatChipInputEvent} from '@angular/material';

@Component({
  selector: 'app-sms',
  templateUrl: './sms.component.html',
  styleUrls: ['./sms.component.scss']
})
export class SmsComponent implements OnInit {
  private formGroup: FormGroup;

  constructor(
    public snackBar: MatSnackBar,
    private account: AccountService,
    private api: ApiService,
    private location: Location,
    private router: Router
  ) {
    
    this.busy = false;
    this.formGroup = new FormGroup({           
      fkMessage : new FormGroup({
        "pkMessageId": new FormControl(Guid.default()),        
        //"contactno": new FormControl(null, [Validators.required]),        
        "message": new FormControl(null, [Validators.required]),
        "contactlist": new FormControl(null, [Validators.required]),              
      }) 
      
    });

   }

  ngOnInit() {
    this.busy = true;
    this.search();
  }

  busy: boolean;
  peoples: any[] = [];

  get form() {
    return this.formGroup;
  }
  get formMessage() {
    return this.form.get('fkMessage');
  }
  // get contactno() {
  //   return this.formMessage.get('contactno');
  // }
  get message() {
    return this.formMessage.get('message');
  }
  get contactlist() {
    return this.formMessage.get('contactlist');
  }

  search() {
    this.api.get(`People?filter=`).subscribe(data => {
      if (data.status === "OK") {
        this.busy = false;
        this.peoples = data.body;
      }
    });
  }

  cancel() {
    this.location.back();
  }

  submit() {
    let mymessage = this.formMessage.value;
        
    this.busy = true;
    this.api.post("Apointments/Sms", mymessage).subscribe(result => {
      this.busy = false;
      if (result.status === "ERROR") {
        this.snackBar.open(result.message, null, {
          duration: 2000
        });
        return;
      }
      
    }, error => {
      this.busy = false;
      this.snackBar.open(error.message, null, {
        duration: 2000
      });
    });
  }

}
