import { ChangeDetectionStrategy, Component, OnInit, Input, ElementRef, ViewChild, ChangeDetectorRef } from '@angular/core';
import { of } from 'rxjs';
import { delay } from 'rxjs/operators';

@Component({
  selector: 'app-tag-input',
  templateUrl: './tag-input.component.html',
  styleUrls: ['./tag-input.component.scss'],
})
export class TagInputComponent implements OnInit {
  constructor() { }

  ngOnInit() {
  }

  @Input() tags: Array<string> = new Array<string>();

  addTag(tf: any) {
    if (this.tags.find(tag => tag === tf.value)) {
      return;
    }

    this.tags.push(tf.value);
    this.updatePadding();

    tf.value = '';
  }

  removeTag(index: number) {
    this.tags.splice(index, 1);
    this.updatePadding();
    
    document.getElementById('tf').focus();
  }

  private updatePadding() {
    of(1).pipe(delay(50)).subscribe(() => {
      document.getElementById('tf').style.paddingLeft = `${document.querySelector("#tags-wrapper").clientWidth + 5}px`;

      if (this.tags.length <= 0) {
        document.getElementById('tf').style.paddingLeft = '12px';
      }
    });
  }
}
