import { NgModule, Injectable } from '@angular/core';
import { Routes, RouterModule, CanActivate, Router, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';

import { Observable } from 'rxjs';

import { SigninComponent } from './user/signin/signin.component';
import { RegistertionComponent } from './user/registertion/registertion.component';
import { HomeComponent } from './home/home.component';
import { ErrorNotFoundComponent } from './_shared/pages/error-not-found/error-not-found.component';

import { ViewerComponent as ReportViewer } from './report/viewer/viewer.component';
import { ListComponent as ReportList } from './report/list/list.component';
import { ListComponent as PatientList } from './patient/list/list.component';
import { FormComponent as PatientDetail } from './patient/form/form.component';
import { ListComponent as ApointmentList } from './apointment/list/list.component';
import { FormComponent as ApointmentForm } from './apointment/form/form.component';
import { WalkinComponent } from './apointment/walkin/walkin.component';
import { ListComponent as UserList } from './user/list/list.component';

import { AccountService } from './_shared/services/account.service';

import { SmsComponent as SmsForm } from './sms/sms.component';

@Injectable()
export class AuthGuard implements CanActivate {
  constructor(
    private account: AccountService,
    private router: Router
  ) {}

  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
      if (this.account.detail == null) {
        this.router.navigate(['/user']);
        return false;
      }

    return true;
  }
}

const routes: Routes = [
  { path: '', redirectTo: 'user', pathMatch: 'full' },
  { 
    path: 'user',
    children: [
      { path: '', redirectTo: 'signin', pathMatch: 'full' },
      { path: 'signin', component: SigninComponent },
      { path: 'register', component: RegistertionComponent }
    ]
  },
  { 
    path: 'home', 
    component: HomeComponent,
    children: [
      { path: '', redirectTo: 'apointment/list', pathMatch: 'full' },
      {
        path: 'apointment',
        children: [
          { path: 'list', component: ApointmentList },
          { path: 'form/:id', component: ApointmentForm },
          { path: 'walkin', component: WalkinComponent }
        ]
      },
      { 
        path: 'patient',
        children: [
          { path: 'list', component: PatientList },
          { path: 'detail/:id', component: PatientDetail }
        ]
      },
      {
        path: 'report',
        children: [
          { path: 'list', component: ReportList },
          { path: 'view', component: ReportViewer }
        ]
      },
      {
        path: 'user',
        children: [
          { path: 'list', component: UserList },
          { path: 'account', component: RegistertionComponent },
          { path: 'account/:id', component: RegistertionComponent }
        ]
      },
      {
        path: 'sms',
        children: [
          { path: 'form', component: SmsForm }          
        ]
      }
    ],
    canActivate: [AuthGuard]
  },
  { path: '**', redirectTo: '404' },
  { path: "404", component: ErrorNotFoundComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule],
  providers: [AuthGuard]
})
export class AppRoutingModule { }
