import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';

import { MatSnackBar } from '@angular/material';

import { Observable } from 'rxjs';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';

import { ApiService } from '../../_shared/services/api.service';
import { AccountService } from '../../_shared/services/account.service';
import { Guid } from '../../_shared/utils/guid';

@Component({
  selector: 'app-registertion',
  templateUrl: './registertion.component.html',
  styleUrls: ['./registertion.component.scss']
})
export class RegistertionComponent implements OnInit {
  private formGroup: FormGroup;

  constructor(
    public snackBar: MatSnackBar,
    private api: ApiService,
    private account: AccountService,
    private route: ActivatedRoute,
    private router: Router
  ) {
    let defaultBDay = new Date();
        defaultBDay.setFullYear(defaultBDay.getFullYear() - 20);

    this.formGroup = new FormGroup({
      'pkUserId': new FormControl(Guid.default()),
      'fkPersonId': new FormControl(Guid.default()),
      'accountType': new FormControl('DOCTOR', [Validators.required]),
      'username': new FormControl(null, [Validators.required, Validators.email]),
      'password': new FormControl(null, [Validators.required, Validators.minLength(6)]),
      'fkPerson': new FormGroup({
        'pkPersonId': new FormControl(Guid.default()),
        'fullName': new FormControl(null, [Validators.required]),
        'firstName': new FormControl(null, [Validators.required]),
        'middleName': new FormControl(null, [Validators.required]),
        'lastName': new FormControl(null, [Validators.required]),
        'gender': new FormControl('MALE', [Validators.required]),
        'dob': new FormControl(defaultBDay.toISOString(), [Validators.required]),
        'address': new FormControl(null, [Validators.required]),
        'picture': new FormControl(null),
      })
    });
  }

  ngOnInit() {
    this.fullName.valueChanges.pipe(
      debounceTime(300),
      distinctUntilChanged()
    ).subscribe((val: string) => {
      this.formGroup.get('fkPerson').get('firstName').setValue(val);
      this.formGroup.get('fkPerson').get('middleName').setValue(val);
      this.formGroup.get('fkPerson').get('lastName').setValue(val);

      if (val != null) {
        if (val.trim() === "") {
          this.title = "New Account";
        } else {
          this.title = val;
        }
      }
    });

    // this.formGroup.get('fkPerson').get('picture').valueChanges.subscribe(val => {
    //   console.log(val);
    // });

    this.mode = this.route.snapshot.params.mode;

    if (this.mode === "update") {
      this.busy = true;
      this.api.get(`Accounts/${this.route.snapshot.params.id}`).subscribe(result => {
        this.busy = false;
        if (result.status === "ERROR") {
          this.snackBar.open(result.message, null, {
            duration: 2000
          });
          return;
        }

        let accountDetail = result.body;

        this.form.patchValue({
          pkUserId: accountDetail.pkUserId,
          fkPersonId: accountDetail.fkPersonId,
          accountType: accountDetail.fkUserType.flag,
          username: accountDetail.username,
          password: accountDetail.password,
          fkPerson: {
            pkPersonId: accountDetail.fkPerson.pkPersonId,
            fullName: accountDetail.fkPerson.fullName,
            firstName: accountDetail.fkPerson.firstName,
            middleName: accountDetail.fkPerson.middleName,
            lastName: accountDetail.fkPerson.lastName,
            gender: accountDetail.fkPerson.gender,
            dob: accountDetail.fkPerson.doB,
            address: accountDetail.fkPerson.address,
            picture: accountDetail.fkPerson.picture
          }
        });
      });
    }
  }

  mode: string = "create";
  title: string = "Account Name";
  busy: boolean = false;

  get form() {
    return this.formGroup;
  }

  get username() {
    return this.formGroup.get('username');
  }

  get password() {
    return this.formGroup.get('password');
  }

  get fullName() {
    return this.formGroup.get('fkPerson').get('fullName');
  }

  get gender() {
    return this.formGroup.get('fkPerson').get('gender');
  }

  get dateOfBirth() {
    return this.formGroup.get('fkPerson').get('dob');
  }

  get address() {
    return this.formGroup.get('fkPerson').get('address');
  }

  get accountType() {
    return this.formGroup.get('accountType');
  }

  submit() {
    let value = this.form.value;

    this.busy = true;
    this.api.post(`Accounts/Register?userTypeFlag=${value.accountType}&personTypeFlag=EMPLOYEE`, value).subscribe(result => {
      this.busy = false;
      if (result.status === "ERROR") {
        this.snackBar.open(result.message, null, {
          duration: 2000
        });
        return;
      }

      this.router.navigate(['home/user/list']);
    }, error => {
      this.busy = false;
      this.snackBar.open(error.message, null, {
        duration: 2000
      });
    });
  }

  update() {
    let value = this.form.value;

    this.busy = true;
    this.api.put(`Accounts?userTypeFlag=${value.accountType}&personTypeFlag=EMPLOYEE`, value).subscribe(result => {
      this.busy = false;
      if (result.status === "ERROR") {
        this.snackBar.open(result.message, null, {
          duration: 2000
        });
        return;
      }

      if (this.route.snapshot.params.reloadAccount) {
        this.account.refreshDetail();
      }
      
      this.router.navigate(['home/user/list']);
    }, error => {
      this.busy = false;
      this.snackBar.open(error.message, null, {
        duration: 2000
      });
    });    
  }
}
