import { Component, OnInit } from '@angular/core';
import { ApiService } from '../../_shared/services/api.service';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {
  constructor(private api: ApiService) { }

  ngOnInit() {
    this.busy = true;

    this.api.get("Accounts/Employee").subscribe(data => {
      if (data.status === "OK") {
        this.busy = false;
        this.users = data.body;
      }
    });
  }

  busy: boolean;
  users: Array<any>;

  query: string = "";  

  search() {
    this.api.get(`Accounts/Employee?filter=${this.query}`).subscribe(data => {
      if (data.status === "OK") {
        this.busy = false;
        this.users = data.body;
      }
    });
  }  
}
