import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';

import { Observable, Subject } from 'rxjs';

import { ApiService } from './api.service';
import { map } from '../../../../node_modules/rxjs/operators';

@Injectable({
    providedIn: 'root'
})
export class AccountService {
    constructor(private api: ApiService) {}

    get detail() {
        return JSON.parse(window.localStorage.getItem("current_user"));
    }

    get token() {
        return window.localStorage.getItem("token");
    }

    refreshDetail() {
        let detail = this.detail;

        if (detail != null) {
            let sub = this.requestDetail(detail.pkUserId).subscribe(e => {
                sub.unsubscribe();
            });
        }
    }

    storeToken(token: string) {
        window.localStorage.setItem("token", token);
    }

    requestDetail(id: string) {
        return this.api.get(`Accounts/${id}`).pipe(map(result => {
            if (result.status === "OK") {
                window.localStorage.setItem("current_user", JSON.stringify(result.body));
            }
            
            return result;
        }));
    }

    logout() {
        window.localStorage.clear();
    }
}