import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';

import {MomentDateAdapter} from '@angular/material-moment-adapter';
import {DateAdapter, MAT_DATE_FORMATS, MAT_DATE_LOCALE} from '@angular/material/core';

import * as moment from 'moment';

import { ChartReadyEvent } from 'ng2-google-charts';

import { ApiService } from '../../_shared/services/api.service';

@Component({
  selector: 'app-viewer',
  templateUrl: './viewer.component.html',
  styleUrls: ['./viewer.component.scss'],
  providers: [
    {provide: DateAdapter, useClass: MomentDateAdapter, deps: [MAT_DATE_LOCALE]},
    { 
      provide: MAT_DATE_FORMATS, useValue: {
        parse: {
          dateInput: 'MMMM YYYY',
        },
        display: {
          dateInput: 'MMMM YYYY'
        }
      } 
    }
  ]
})
export class ViewerComponent implements OnInit {
  @ViewChild('cchart') cchart;

  constructor(
    private api: ApiService,
    private route: ActivatedRoute
  ) {
    
  }

  ngOnInit() {
    this.title = this.route.snapshot.params.name;
    this.flag = this.route.snapshot.params.flag;
    this.path = this.route.snapshot.params.path;

    this.requestData();

    //console.log(this.cchart);
  }

  title: string = "View";
  flag: string = "NONE";
  path: string = "";

  hasResult: boolean = false;
  chartReady: boolean = false;

  date: FormControl = new FormControl(moment());

  onMonthSelected(evt, picker) {
    picker.close();
    this.date.patchValue(evt);
    this.requestData();
  }

  onChartReady(event: ChartReadyEvent) {
    this.chartReady = true;
  }

  tableData = [];

  pieChartData = {
    chartType: 'PieChart',
    dataTable: [
      ['Name', 'Total']
    ],
    options: {'title': 'Diagnosis'},
  };

  private requestData() {
    this.hasResult = false;

    this.tableData = [];
    //this.pieChartData.dataTable = [['Name', 'Total']];

    this.api.get(`reports/${this.path}/${this.date.value.format("MMMM DD YYYY")}`).subscribe(data => {
      if (data.status === "OK") {
        this.tableData = data.body.table;

        //data.body.pie.forEach(d => {
        //  this.pieChartData.dataTable.push([d.item1, d.item2]);
        //});

        this.hasResult = true;
      }
    });
  }
}
