import { Component, OnInit, Optional, forwardRef } from '@angular/core';
import { NgControl, NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';
import { AngularFireStorage } from 'angularfire2/storage';

@Component({
  selector: 'app-media-picker',
  templateUrl: './media-picker.component.html',
  styleUrls: ['./media-picker.component.scss'],
  providers: [{
    provide: NG_VALUE_ACCESSOR,
    multi: true,
    useExisting: forwardRef(() => MediaPickerComponent)
  }]
})
export class MediaPickerComponent implements OnInit, ControlValueAccessor {
  private _file: File;
  private _previewUrl;

  constructor(
    private afstorage: AngularFireStorage
  ) {
    this.busy = false;
    this.hasPreview = false;
  }

  ngOnInit() {
  }
  writeValue(obj: any): void {
    this.previewUrl = obj;
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }
  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }
  setDisabledState?(isDisabled: boolean): void {
    //throw new Error("Method not implemented.");
  }

  busy: boolean;
  hasPreview: boolean;

  onChange: any = () => { };
  onTouched: any = () => { };

  get file() {
    return this._file;
  }

  get previewUrl() {
    return this._previewUrl;
  }
  set previewUrl(val) {
    this._previewUrl = val;
    this.hasPreview = true;
  }

  fileChanged(el: HTMLInputElement) {
    if (el.files && el.files[0]) {
      this._file = el.files[0];

      let path = `/temp/${this._file.name}`;
      let ref = this.afstorage.ref(path);
      
      ref.put(this._file).then(() => {
        let sub = ref.getDownloadURL().subscribe(url => {
          this.writeValue(url);
          this.onChange(url);
          this.onTouched();

          this.busy = false;

          sub.unsubscribe();
        });
      });

      this.busy = true;
    }
  }
}
