import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';

import { MatSnackBar } from '@angular/material';

import { ApiService } from '../../_shared/services/api.service';
import { AccountService } from '../../_shared/services/account.service';

@Component({
  selector: 'app-signin',
  templateUrl: './signin.component.html',
  styleUrls: ['./signin.component.scss']
})
export class SigninComponent implements OnInit {
  private formGroup: FormGroup;

  constructor(
    public snackBar: MatSnackBar,
    private api: ApiService,
    private account: AccountService,
    private route: Router
  ) { 
    this.formGroup = new FormGroup({
      'username': new FormControl(null, [Validators.required, Validators.email]),
      'password': new FormControl(null, [Validators.required])
    });
  }

  ngOnInit() {
  }

  get form() {
    return this.formGroup;
  }

  get username() {
    return this.form.get('username');
  }

  get password() {
    return this.form.get('password');
  }

  login() {
    let value = this.form.value;

    this.api.post("Accounts/Login/Web", value).subscribe(result => {
      if (result.status === "ERROR") {
        this.snackBar.open(result.message, null, {
          duration: 2000
        });
        return;
      }

      this.account.storeToken(result.body.token);
      this.api.addHeader('Authorization', `Bearer ${this.account.token}`);

      this.account.requestDetail(result.body.id).subscribe(result => {
        if (result.status === "ERROR") {
          this.snackBar.open(result.message, null, {
            duration: 2000
          });
          return;
        }

        this.route.navigate(["/home"]);
      }, error => {
        this.snackBar.open(error.message, null, {
          duration: 2000
        });  
      });
    }, error => {
      this.snackBar.open(error.message, null, {
        duration: 2000
      });
    });
  }
}
