import { Component, OnInit, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  @Input() title: string;
  @Input() busy: boolean;
  
  paths: Array<string>;

  constructor(private activeRoute: ActivatedRoute) {
    this.busy = false;
  }

  ngOnInit() {
    this.paths = this.buildBreadCrumb(this.activeRoute);
  }

  private buildBreadCrumb(route: ActivatedRoute, path: Array<string> = new Array<string>()): Array<string> {
    if (route != null) {
      if (route.parent != null) {
        path = path.concat(this.buildBreadCrumb(route.parent));
      }

      if (route.url != null) {
        route.url.forEach((segments) => {
          segments.forEach(segment => {
            if (segment.path !== "") {
              path.push(segment.path);
            }
          });
        });
      }

      // if (route.routeConfig != null) {
      //   path.push(route.routeConfig.path);
      // }
    }

    return path;
  }
}
