import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { MatSnackBar } from '@angular/material';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';

import * as moment from 'moment';

import { Guid } from '../../_shared/utils/guid';
import { ApiService } from '../../_shared/services/api.service';
import { AccountService } from '../../_shared/services/account.service';

@Component({
  selector: 'app-walkin',
  templateUrl: './walkin.component.html',
  styleUrls: ['./walkin.component.scss']
})
export class WalkinComponent implements OnInit {
  private formGroup: FormGroup;

  constructor(
    public snackBar: MatSnackBar,
    private account: AccountService,
    private api: ApiService,
    private location: Location,
    private router: Router
  ) { 
    let age = 20;
    let defaultBDay = new Date();
        defaultBDay.setFullYear(defaultBDay.getFullYear() - age);

    this.busy = false;
    this.formGroup = new FormGroup({
      pkApointmentId: new FormControl(Guid.default()),
      fkCreatorId: new FormControl(account.detail.fkPersonId),
      fkPatient: new FormGroup({
        'pkPersonId': new FormControl(Guid.default()),
        'fullName': new FormControl(null, [Validators.required]),
        'firstName': new FormControl(null, [Validators.required]),
        'middleName': new FormControl(null, [Validators.required]),
        'lastName': new FormControl(null, [Validators.required]),
        'gender': new FormControl('MALE', [Validators.required]),
        'dob': new FormControl(defaultBDay.toISOString(), [Validators.required]),
        'age': new FormControl(age),
        'contactno': new FormControl(null, [Validators.required]),
        'email': new FormControl(null, [Validators.email]),
        'address': new FormControl(null, [Validators.required]),
      }),
      createdDate: new FormControl(new Date()),
      apointmentDate: new FormControl(new Date()),
      patientSymptoms: new FormControl(null, [Validators.required]),
      isApproved: new FormControl(false),
    });

    this.fullName.valueChanges.pipe(
      debounceTime(300),
      distinctUntilChanged()
    ).subscribe((val: string) => {
      this.person.get('firstName').setValue(val);
      this.person.get('middleName').setValue(val);
      this.person.get('lastName').setValue(val);

      if (!this.valueFromAutoComplete) {
        this.selectedPerson = null;
        this.person.get('pkPersonId').setValue(Guid.default());
      }

      this.valueFromAutoComplete = false;
    });

    this.person.get('dob').valueChanges.subscribe(val => {
      this.person.get('age').setValue(moment(new Date()).diff(val, 'year'));
    });
  }

  ngOnInit() {
    this.api.get("People").subscribe(result => {
      this.people = result.body;
    });
  }

  busy: boolean;
  valueFromAutoComplete: boolean;
  selectedPerson: any;
  people: any[];

  get form() {
    return this.formGroup;
  }

  get person() {
    return this.form.get('fkPatient');
  }

  get fullName() {
    return this.person.get('fullName');
  }

  get contactno() {
    return this.person.get('contactno');
  }

  get email() {
    return this.person.get('email');
  }

  get address() {
    return this.person.get('address');
  }

  cancel() {
    this.location.back();
  }

  submit() {
    let apointment = this.form.value;
    
    if (this.selectedPerson != null) {
      apointment.fkPatientId = this.selectedPerson.pkPersonId;
      apointment.fkPatient = null;
    } else {
      apointment.fkPatient.fkPersonEntityTypeId = "40EA2061-C315-4EEC-8219-D056E91DD8AD";
    }

    this.busy = true;
    this.api.post("Apointments", apointment).subscribe(result => {
      this.busy = false;
      if (result.status === "ERROR") {
        this.snackBar.open(result.message, null, {
          duration: 2000
        });
        return;
      }

      this.location.back();
    }, error => {
      this.busy = false;
      this.snackBar.open(error.message, null, {
        duration: 2000
      });
    });
  }

  displayFn(person?: any): string | undefined {
    return person ? person.fullName : undefined;
  }

  onPersonSelected(evt) {
    let person = evt.option.value;

    this.valueFromAutoComplete = true;
    this.selectedPerson = person;
    this.form.patchValue({
      'fkPatient': {
        'pkPersonId': person.pkPersonId,
        'gender': person.gender,
        'dob': person.doB,
        'contactno': person.contactNo,
        'email': person.email,
        'address': person.address
      }
    });
  }
}
